const ProfilePage = ({ setPage,mainPage,setMainPage }) => {
	const { sso }  = useSelector(state => state);
	const { user,evsdata } = sso;
	const dispatch = useDispatch()
    const { setting } = useSelector( state => state.theme )
	const { REACT_APP_THEME_TAG : themetag } = process.env; 
	const ui = setting.clients.find( r => r.tag === themetag )
    const { REACT_APP_API_URL : API_URL } = process.env; 
	const [ Logo,setLogo ] = useState(null)
	//const [ mainpage,setMainPage ] = useState('candidate')

	const PageRenderer = () => {
	   switch(mainPage){
		 case 'control' : return <ControlPage mainPage={mainPage}/>; break;
		 case 'elector' : return <ElectorPage mainPage={mainPage}/>; break;
		 case 'portfolio' : return <PortfolioPage mainPage={mainPage}/>; break;
		 case 'candidate' : return <CandidatePage mainPage={mainPage}/>; break;
		 default: return <ControlPage />
	   }
	}
	
	useEffect(() => {
		import(`../../../assets/img/logo/${themetag}/logo.png`).then(src => setLogo(src.default))
	},[])

	return <PageRenderer/>
	   
}


